import User from './User'
import React from 'react'
import {Component} from 'react'
import axios from 'axios'
import storeFactory from '../store'
import {serverLink} from '../constants'
import HeadComp from './HeadComp'

export default class UserStore extends Component {

    state = {loading: 1}

    user = null
    incomingData = null

    static getInitialProps({ query: { id } }) {

        this.user = id

    }

    componentDidMount() {

        axios(serverLink + '/data/' + id).then(res => this.incomingData = res.data)
    }

    render() {

        const {products, customerMeta, customerML} = this.props.incomingData
        const store = storeFactory({products, customerMeta, customerML})

        return (
            <div>
                <HeadComp/>
                <User store={store}/>
            </div>
        )
    }
}


